@mixin shadow{
    filter: drop-shadow(0px 0px 2px rgba(0,0,0,0.3));
    -webkit-filter: drop-shadow(0px 0px 2px rgba(0,0,0,0.3));
}
  
@mixin borderRadius{
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}
  